body {
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    color: #6c7279;
    background-color: #fff;
    -webkit-font-smoothing:antialiased;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0 0 10px;
}

/*Container*/
.container  {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

/*Header*/
.header {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    height: 100px;
}

.header.fixed {
    position: fixed;
    background-color: #31344e;
    height: auto;
}

.header.fixed .header__inner {
    padding: 15px 0;
    border-bottom: 0;

}

.header__inner {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #515369;
    padding: 35px;
    align-items: center;
}

/*Nav*/
.nav {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    display: flex;
    transition: .3s;

}



.nav__link {
    margin-left : 50px;
    color: white;
    text-decoration: none;
    opacity: .75;
    transition: .1s;
}


.nav__link:first-child {
    margin-left: 0;
}

.nav__link:hover {
    opacity: 1;
}

/*INTRO*/

.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 750px;
    background: #31344e url("images/intro_bg.jpg") center no-repeat;
    padding-top: 100px;
    background-size: cover;
}

.intro__inner {
    width: 100%;
    max-width:970px;
    margin: 0 auto;
    text-align: center;
}

.intro__title {
    font-family: 'Open Sans', sans-serif;
    font-size: 65px;
    color: white;
    font-weight: 700;
    margin: 0 0 30px;
    text-transform: uppercase;
    line-height: 1.1;
}

.intro__subtitle {
    font-size: 22px;
    color: white;
    line-height: 1.5;
    font-weight: 400;
    margin-bottom: 60px;
}


/*Button*/

.btn {
    font-family: inherit;
    text-transform: uppercase;
    display: inline-block;
    vertical-align: top;
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    text-decoration:none;
    padding: 14px 40px;
    border-radius: 2px;
    border: 0;
    cursor: pointer;
    text-align: center;
    transition: background .1s linear;
}

.btn--red {
    background-color: #e84545;
}

.btn--red:hover {
    background-color: #c43636;
}

.btn--long {
    min-width: 280px;
}

/*FEATURES*/

.features {
    display: flex;
    flex-wrap: wrap;
    margin: 95px 0;
}

.features__item {
    width: 33.33333%;
    margin: 25px 0;
    text-align: center;
    padding: 0 40px;
}

.features__icon {
    margin-bottom: 25px;
}

.features__title {
    font-family: Raleway;
    font-size: 14px;
    color: #2d3033;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.features__text {
    font-size: 14px;
    color: #6c7279;
    line-height: 1.5;

}


/*WORKS*/

.works {
    display: flex;
    flex-wrap: wrap;

}

.works__item {
    width: 25%;
    position: relative;
    overflow: hidden;
    height: 350px;
    border-bottom: 1px solid #ffffff;
    border-right: 1px solid #ffffff;
}

.works__item:hover .works__content {
    opacity: 1;
}

.works__photo {

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    min-width: 100%;
    transform: translate3d(-50%, -50%, 0);
}

.works__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 2;
    background-color: rgba(232, 69, 69, .9);
    opacity: 0;
    transition: opacity 0.2s linear;

}

.works__title {
    font-size: 14px;
    font-weight: 700;
    color: white;
    text-transform: uppercase;
    margin-bottom: 5px;
}

.works__text {
    font-size: 14px;
    color: white;
}


/*TEAM*/
.team {
    margin: 100px 0 70px;
}

.team__inner {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
}

.team__item {
    width: 25%;
    padding: 0 15px;
    margin-bottom: 30px;
}

.team__photo {
    display: block;
    max-width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.team__name {
    font-size: 22px;
    color: #2d3033;
    margin-bottom: 8px;
}

.team__prof {
    text-transform: uppercase;
    font-size: 13px;
    color: #e84545;
    margin-bottom: 15px;
}

.team__text {
    margin-bottom: 20px;
    font-size: 14px;
    line-height: 1.5;
    color: #6c7279;
}

/*social*/
.social {
    display: flex;
}

.social--footer {
    justify-content: center;

}

.social__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-right: 4px;
    border: 1px solid #e8ecee;
    background-color: #ffffff;
    transition: background .1s linear;
}

.social__item:hover {
    background-color: #e8ecee;
}

.social--footer .social__item {
    background-color:transparent;
    border-width: 2px;
    border-color: #ffffff;
    border-radius: 2px;

}

.social--footer .social__item:hover {
    border-color: #ffffff;
}

.social__icon {
    fill: #c6cacc;
    display: block;
    height: 18px;
    transition: fill .1s linear;
}

.social--footer .social__icon {
    fill: #ffffff;
}

.social__item:hover .social__icon {
    fill: #000;
}


/*reviews*/

.reviews {
    overflow: hidden;
    background-color: #53354a;
}

.reviews__slider {
    height: 500px;
    overflow: hidden;
}

.reviews__slider.slick-initialized {
    height: auto;
}

.reviews__item {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
}

.reviews__photo {
    width: 50%;
    height: 500px;
    position: relative;
}

.reviews__img {
    position: absolute;
    top: 50%;
    right: 10px;
    z-index: 1;
    transform: translateY(-50%);
}

.reviews__img2 {
    position: absolute;
    width: 100%;
    right: 20px;
    z-index: 1;
   /* transform: translateY(-25%) translateX(5%);*/

}

.reviews__content {
    width: 50%;
    padding-left: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.reviews__text {
    margin-bottom: 25px;
    font-family: 'Cardo', sans-serif;
    font-style: italic;
    font-size: 36px;
    line-height: 1.2;
    color: #ffffff;
}

.reviews__author {
    font-size: 13px;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
}

.slick-dots {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0 0 10px;
    list-style: none;
    width: 50%;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 2;
}

.slick-dots li {
    margin: 0 5px;
}

.slick-dots button {
    width: 10px;
    height: 10px;
    padding: 0;
    background: #fff;
    border-radius: 50%;
    border: 0;
    cursor: pointer;
    font-size: 0;
    color: transparent;
    opacity: .5;
}

.slick-dots button:focus {
    outline: 0;
}

.slick-dots .slick-active button {
    opacity: 1;
}

    /*DOWNLOAD*/

.download {
    margin: 90px 0;
    text-align: center;
}

.download__title {
    font-size: 28px;
    color: #2d3033;
    font-weight: 300;
    margin-bottom: 10px;
}

.download__text {
    font-size: 13px;
    color: #2d3033;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 30px;
}

/*FOOTER*/

.footer {

    background-color: #3a3e64 ;
}

.footer__inner {
    display: flex;
    flex-wrap: wrap;
    padding: 70px 0;

}

.footer__block {
    width: 33.333333%;
    text-align: center;
    padding: 0 15px;
}

.footer__title {
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    color: #fff;

}

.footer__adress {
    color: rgba(255,255,255,.5);
    font-style: normal;
    font-size: 14px;
    line-height: 1.5;
}


.footer__text {
    color: rgba(255,255,255,.5);
    font-size: 14px;
    line-height: 1.5;
}


/*COPYRIGHT*/
.copyright {
    background-color: #313454;
    padding: 20px 0;
}

.copyright__text {
    text-align: center;
    font-size: 14px;
    color:#808080;
    line-height: 1.5;
}

.copyright__text span {
    color: #ffffff;
}

/*BURGER*/

.burger {
    display: none;
    background: none;
    border: 0;
    padding: 9px 2px;
    cursor: pointer;
}

.burger__item {
    display: block;
    width: 28px;
    height: 3px;
    background-color: white;
    font-size: 0px;
    color: transparent;
    position: relative;

}

.burger__item:before,
.burger__item:after {
    content: '';

    height: 100%;
    position: absolute;
    background-color: white;
    right: 0px;
    z-index: 1;
}

.burger__item:before {
    width: 30px;
    top: -8px;
}

.burger__item:after {
    width: 26px;
    bottom: -8px;
}


/*MEDIA*/

@media (max-width: 1300px)  {
    /*works*/
    .works__item {
        width: 50%;
    }
}


@media (max-width: 991px) {
    /*nav*/
    .nav {
        display: none;
        position: absolute;
        right: 0;
        top: 100%;
        width: 100%;
        text-align: right;
        flex-direction: column;
        background-color: #31344e;

    }

    .nav.show {
        display: flex;

    }

    .nav__link {
        padding: 9px 15px;

    }

    /*Burger*/
    .burger {
        display: block;

    }

    /*intro*/
    .intro__title {
        font-size: 40px;
    }

    /*features*/
    .features__item {
        width: 50%;
    }

    /*team*/
    .team__item {
        width: 50%;
        text-align: center;
    }

    .team__photo {
        margin-left: auto;
        margin-right: auto;
    }

    /*social*/
    .social {
        justify-content: center;
    }

    /*Reviews*/

    .reviews__photo {
        width: 100%;
        margin: 15px 0 40px;
        overflow: hidden;
    }

    .reviews__content {
        width: 100%;
        padding-left: 0;
        padding-bottom: 40px;
    }

}

@media (max-width: 767px) {
    .intro {
        height: auto;
        padding-top: 130px;
        padding-bottom: 30px;
    }

    .intro__title {
        font-size: 30px;
    }

    .intro__subtitle {
        font-size:16px;
        margin-bottom: 30px;
    }

    /*features*/
    .features {
        margin: 40px 0;
    }

    .features__item {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
    /*works*/
    .works__item {
        width: 100%;
        height: 300px;
    }

    /*team*/
    .team__item {
        width: 100%;
    }

    .team {
        margin: 60px 0 30px 0;
    }

    /*reviews*/
    .reviews__text {
        font-size: 25px;
    }
    /*button*/
    .btn--long {
        min-width: 260px;
    }

    /*Download*/
    .download {
        margin:50px 0;
    }

    .download__title {
        font-size: 24px;
    }

    /*footer*/

    .footer__block {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 30px;
    }

    .footer__block:last-child {
        margin-bottom: 0;
    }

    .footer__inner {
        padding: 30px 0;
    }

}

@media (max-width:414px) {
    /*reviews*/
    .reviews__img {
        right: -100px;
    }
}